import {slideToRight} from "./animation";

(() => {
    var indexBanner = new Swiper('.banner', {
        speed: 400,
        spaceBetween: 0,
        loop: true,
        navigation: {
            nextEl: '.banner__control.button-control-next',
            prevEl: '.banner__control.button-control-prev',
        },
    });

    slideToRight('.banner__slide.swiper-slide-active');
    indexBanner.on('slideChangeTransitionEnd', () => slideToRight('.banner__slide.swiper-slide-active'));

    var indexWorks = new Swiper('.works__slider', {
        speed: 400,
        spaceBetween: 0,
        loop: true,
        autoHeight: true,
        navigation: {
            nextEl: '.works__control.button-control-next',
            prevEl: '.works__control.button-control-prev',
        },
    });

    var indexReviews = new Swiper('.reviews__slider', {
        speed: 400,
        spaceBetween: 30,
        loop: true,
        navigation: {
            nextEl: '.reviews__control.button-control-next',
            prevEl: '.reviews__control.button-control-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: 1
            },
            1024: {
                slidesPerView: 2
            },
            1600: {
                slidesPerView: 3
            },
        }
    });

    var indexPartners = new Swiper('.partners__slider', {
        speed: 400,
        spaceBetween: 1,
        loop: true,
        navigation: {
            nextEl: '.partners__control.button-control-next',
            prevEl: '.partners__control.button-control-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: 1
            },
            600: {
                slidesPerView: 2
            },
            768: {
                slidesPerView: 3
            },
            1024: {
                slidesPerView: 4
            },
            1201: {
                slidesPerView: 5
            },
            1601: {
                slidesPerView: 6,
            }
        }
    });

    var worksDetail = new Swiper('.works-detail__slider', {
        speed: 400,
        spaceBetween: 30,
        loop: true,
        navigation: {
            nextEl: '.works-detail__control.button-control-next',
            prevEl: '.works-detail__control.button-control-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: 1
            },
            361: {
                slidesPerView: 2
            },
            601: {
                slidesPerView: 3
            },
            769: {
                slidesPerView: 4
            }
        }
    });

    var newsSlider = new Swiper('.news__slider', {
        speed: 400,
        spaceBetween: 30,
        loop: true,
        navigation: {
            nextEl: '.news__control.button-control-next',
            prevEl: '.news__control.button-control-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: 1
            },
            601: {
                slidesPerView: 2
            },
            1201: {
                slidesPerView: 3
            }
        }
    });

    var certificatesSlider = new Swiper('.certificates__slider', {
        speed: 400,
        spaceBetween: 30,
        loop: true,
        navigation: {
            nextEl: '.certificates__control.button-control-next',
            prevEl: '.certificates__control.button-control-prev',
        },
        breakpoints: {
            320: {
                slidesPerView: 1
            },
            361: {
                slidesPerView: 2
            },
            601: {
                slidesPerView: 3
            },
            769: {
                slidesPerView: 4
            }
        }
    });
})();
