const controller = new ScrollMagic.Controller({
    refreshInterval: 0
});

export const slideToRight = (parentClass) => {
    const parents = document.querySelectorAll(parentClass);

    parents && parents.forEach((parent) => {
        const elements = parent && parent.querySelectorAll('.slideToRight');

        elements && elements.forEach((item, i) => {
            const toRight = new TimelineMax()
                // .from(item, 1, {x: '-120%', opacity: 0}, )
                .to(item, 1.2, {x: 0, opacity: 1, ease: 'power1.in'}, (i + 1) / 4);

            new ScrollMagic.Scene({
                triggerElement: parent,
                triggerHook: .8
            })
                .setTween(toRight)
                .reverse(false)
                .addTo(controller)
            // .addIndicators()
        });
    });
};

const slideToTop = () => {
    const elements = document.querySelectorAll('.slideToTop');
    elements && elements.forEach((item) => {
        const toRight = new TimelineMax()
            .from(item, 1.2, {y: '100%', opacity: 0, ease: 'power1.in'})
            .to(item, 1.2, {y: 0, opacity: 1, ease: 'power1.in'});

        new ScrollMagic.Scene({
            triggerElement: item,
            triggerHook: 1
        })
            .setTween(toRight)
            .reverse(false)
            .addTo(controller)
        // .addIndicators({name: 'to top'})
    });
};

export const scrollAddClass = (elemClass, nameClass) => {
    const elements = document.querySelectorAll(elemClass);

    elements && elements.forEach(el => {
        new ScrollMagic.Scene({
            triggerElement: el,
            triggerHook: 1,
            offset: -50
        })
            .setClassToggle(el, nameClass)
            .reverse(false)
            .addTo(controller)
            // .addIndicators()
    })
};

scrollAddClass('.feedback__form', 'feedback__form_active');
slideToRight('.catalog__wrapper');
slideToRight('.services__wrapper');
slideToRight('.our-works__item-bottom');


if(window.matchMedia('(min-width: 768px)').matches) {
    slideToTop()
}
